.card {
  width: $cardwidth;
  background-color: $base-color;
  border: 1px solid $base-border-color;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  padding: 32px 16px 16px;
  transition-duration: $duration;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    height: 134px;
    width: 160px;
    align-self: center;
    margin-bottom: 40px;
  }

  &__code {
    width: fit-content;
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    color: $sub-text-color;
    margin-bottom: 16px;
  }

  &__raiting {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__reviews {
    align-self: last baseline;
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    color: $main-text-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    line-height: 18px;
    margin-bottom: 16px;
  }

  &__price-title {
    font-size: 12px;
    font-weight: 400;
    color: $sub-text-color;
  }

  &__price-value {
    font-size: 16px;
    font-weight: 700;
    color: $main-text-color;
  }

  &__button {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    align-items: center;
    color: $base-color;

    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
    text-transform: uppercase;
    padding: 12px;

    background-color: $moyo-color;
    border-radius: 5px;
    transition-duration: $duration;

    &:hover {
      color: #00acdc;
      background-color: $base-color;
      border: 1px solid $moyo-color;
    }
  }
}

.card__title {
  font-size: 12px;
  line-height: 18px;
  font-weight: 500;
  color: $main-text-color;
  margin-bottom: 4px;
}

.card:hover .card__title {
  color: #34568b;
}
